import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { UserService } from '../../service/user.service';
import { Headers, Http } from '@angular/http';
import { FileOpener } from '@ionic-native/file-opener';
import { Transfer, TransferObject } from '@ionic-native/transfer';
import { File } from '@ionic-native/file';

@IonicPage()
@Component({
  selector: 'page-setting',
  templateUrl: 'setting.html',
})
export class Setting {

  Version = '';
  ishide:boolean = true;
  apkDownloadUrl='';

  fileTransfer: TransferObject;

  constructor(public fileOpener: FileOpener, public transfer: Transfer, public http: Http,public navCtrl: NavController, public navParams: NavParams, public UserService: UserService, public alertCtrl: AlertController, public file: File) {
    this.Version = this.UserService.Version;
    if(this.UserService._user._id){
      this.ishide = false;
    }
  }

  out() {
    this.UserService.clearStorage();
    this.navCtrl.pop();
  }

  //app
  getAppVersion() {
    this.UserService.presentLoadingDefault();
    let url = "http://www.devonhello.com/chihu/appversion";

    var headers = new Headers();
    headers.append('Content-Type', 'application/x-www-form-urlencoded');

    this.http.post(url, "", {
      headers: headers
    })
      .subscribe((res) => {
        //alert(res.json()[0]["v"]);
        if (res.json()[0]["v"] != this.UserService.Version) {
          //
          this.apkDownloadUrl = res.json()[0]["url"];
          this.fileTransfer = this.transfer.create();
          this.AppV();
        }else{
          this.UserService.presentLoadingDismiss();
          this.UserService.showAlert("...");
        }
      });
  }

  //
  download() {
    var _that = this;
    var apkurl = this.file.externalDataDirectory + 'chihu.apk';
    this.fileTransfer.download(this.apkDownloadUrl, apkurl).then((entry) => {
      //apk
      this.fileOpener.open(apkurl, 'application/vnd.android.package-archive')
        .then(() => console.log('File is opened'))
        .catch(e => alert('Error' + e));

    }, (error) => {
      // handle error
    });

  }

  AppV() {

    this.UserService.presentLoadingDismiss();
    let alert = this.alertCtrl.create({
      title: '',
      message: '?',
      buttons: [
        {
          text: '',
          role: 'cancel',
          handler: () => {
            //console.log('Cancel clicked');
          }
        },
        {
          text: '',
          handler: () => {
            this.download();
            this.UserService.showAlert("...");
          }
        }
      ]
    });

    alert.present();
  }

}



